import Axios from 'axios';

class SumateService {
    get token() {
        return localStorage.getItem('token') || '';
    }

    get headers() {
        return {
            headers: {
                token: this.token
            }
        };
    }

    async getSumate() {
        return await Axios.get(
            `${process.env.REACT_APP_URL}/sumate`,
            this.headers
        )
            .then(({ data }) =>
                data.sumate.filter((noticias) => noticias._id).reverse()
            )
            .catch((e) => {
                console.log(e);
                return [];
            });
    }

    async crearSumate(sumate) {
        return await Axios.post(
            `${process.env.REACT_APP_URL}/sumate`,
            sumate,
            this.headers
        ).then((res) => {
            if (res.ok)
                return alert('envio con exito, te contactaremos en breve !!');
            else return alert('error al enviar el formulario :( ');
        });
    }

    borrarSumate(_id) {
        const url = `${process.env.REACT_APP_URL}/sumate/${_id}`;
        return Axios.delete(url, this.headers)
            .then(() => alert('Borrado', '', 'success'))
            .catch((err) => console.log(err));
    }

    async actualizarSumate(id, newSumate) {
        return await Axios.put(
            `${process.env.REACT_APP_URL}/sumate/${id}`,
            newSumate,
            this.headers
        ).catch((e) => console.log(e));
    }
}

export default new SumateService();
