import { useState } from 'react';
import {
    Card,
    CardActions,
    CardContent,
    CardHeader,
    CardMedia,
    IconButton,
    makeStyles,
    Typography
} from '@material-ui/core';
import colores from '../styles/colors';
import noimg from '../assets/noimg.jpg';
import { isMobile } from 'react-device-detect';
import CloseIcon from '@material-ui/icons/Close';

const CardNoticia = ({ data, setClickNota }) => {
    const [shadow, setShadow] = useState(1);
    const classes = useStyles();

    return (
        <Card
            onClick={() => setClickNota(data)}
            elevation={shadow}
            className={classes.cardd}
            onMouseEnter={() => setShadow(3)}
            onMouseLeave={() => setShadow(1)}
        >
            <CardHeader
                title={data.title.slice(0, 81)}
                subheader={data.subtitle.slice(0, 99)}
                titleTypographyProps={{ variant: isMobile ? 'h6' : 'h5' }}
            />

            <CardMedia image={data.img || noimg} className={classes.mediaimg} />
            <CardContent style={{ minHeight: !isMobile && 120 }}>
                {!isMobile && (
                    <Typography className={classes.letratxt}>
                        {data.text.slice(0, 240)}
                    </Typography>
                )}
            </CardContent>
            <CardActions
                style={{
                    display: 'flex',
                    flexDirection: 'row-reverse',
                    marginRight: 20
                }}
            >
                <Typography> {data.date && data.date.slice(0, 10)}</Typography>
            </CardActions>
        </Card>
    );
};

const useStyles = makeStyles(() => ({
    letratxt: {
        // marginBottom: 10
    },
    cardd: {
        marginTop: isMobile ? 15 : 30,
        backgroundColor: colores.mainLightfull,
        borderRadius: 10,
        //width: 600,
        margin: 'auto',
        cursor: 'pointer',
        overflow: 'auto'
    },
    mediaimg: {
        height: isMobile ? 160 : 260,
        marginRight: isMobile ? 10 : 25,
        marginLeft: isMobile ? 10 : 25,
        borderRadius: 15
    }
}));

export default CardNoticia;
