import React from 'react';

import { Paper, Grid, Typography, Card, CardMedia } from '@material-ui/core';
import provStyles from '../../styles/provStyle';
import { incluirData } from '../../info/provincia';
import jorincluir1 from '../../assets/jor/jorincluir1.jpg';
import jorincluir2 from '../../assets/jor/jorincluir2.jpg';
import jorincluir3 from '../../assets/jor/incluir3.jpg';
import jorincluir4 from '../../assets/jor/incluir4.jpg';
import { isMobile } from 'react-device-detect';

const VacunasContent = () => {
    const classes = provStyles();

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={12}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 120 : 220,
                            borderRadius: 15
                        }}
                    >
                        <CardMedia
                            image={jorincluir1}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
                <Grid item sm={8}>
                    <Grid item sm={12}>
                        <Paper elevation={3} className={classes.cards}>
                            <Typography className={classes.textmed}>
                                {incluirData.text}
                            </Typography>
                        </Paper>
                    </Grid>
                </Grid>

                <Grid item xs={12} sm={4}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 110 : '100%',
                            borderRadius: 15
                        }}
                    >
                        <CardMedia
                            image={jorincluir2}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 110 : 220,
                            borderRadius: 15
                        }}
                    >
                        <CardMedia
                            image={jorincluir4}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 110 : '100%',
                            borderRadius: 15
                        }}
                    >
                        <CardMedia
                            image={jorincluir3}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

export default VacunasContent;
