import { Backdrop, CircularProgress } from '@material-ui/core';
import React, { createContext, Component } from 'react';
import noticiasService from '../helpers/noticiasService';
import proyectoService from '../helpers/proyectoService';

export const FormContext = createContext();

class FormProvider extends Component {
    constructor(props) {
        super(props);

        this.state = {
            ready: false,
            noticias: []
        };
    }

    async componentDidMount() {
        const noticias = await noticiasService.getNoticias();
        const proyectos = await proyectoService.getProyectos();

        this.setState({
            noticias: noticias,
            proyectos: proyectos,
            ready: true
        });
    }

    render() {
        if (!this.state.ready)
            return (
                <Backdrop open={true}>
                    <CircularProgress color="inherit" />
                </Backdrop>
            );

        return (
            <FormContext.Provider
                value={{
                    ...this.state
                }}
            >
                {this.props.children}
            </FormContext.Provider>
        );
    }
}

export default FormProvider;
