import { useState } from 'react';
import {
    Avatar,
    Card,
    CardContent,
    CardHeader,
    CardMedia,
    Fab,
    Grid,
    Typography
} from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import defaultImage from '../assets/noimg.jpg';
import colores from '../styles/colors';
import EcoIcon from '@material-ui/icons/Eco';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import fem from '../assets/iconos/fem.png';
import DomainIcon from '@material-ui/icons/Domain';

const CardProyect = ({ proyect, setOpenProyect }) => {
    const [shadow, setShadow] = useState(2);

    function handlerIcon() {
        switch (proyect.eje) {
            case 'ambiente':
                return (
                    <Fab>
                        <EcoIcon />
                    </Fab>
                );
            case 'barrios':
                return <DomainIcon />;
            case 'genero':
                return (
                    <Avatar
                        src={fem}
                        style={{
                            height: 24,
                            width: 24,
                            margin: 'auto',
                            marginTop: 2
                        }}
                    />
                );
            case 'produccion':
                return <EqualizerIcon />;
            default:
                return <EqualizerIcon />;
        }
    }

    return (
        <Card
            elevation={shadow}
            style={{
                minHeight: isMobile ? 420 : 460,
                textAlign: 'center',
                borderRadius: 15,
                cursor: 'pointer'
            }}
            onMouseEnter={() => setShadow(5)}
            onMouseLeave={() => setShadow(1)}
            onClick={() => setOpenProyect(proyect)}
        >
            <div style={{ backgroundColor: proyect.color, padding: '7%' }}>
                <Fab>{handlerIcon()}</Fab>
                <Typography
                    style={{
                        color: colores.darkBlueNav,
                        fontSize: 14,
                        letterSpacing: 3,
                        marginTop: 10
                    }}
                >
                    {proyect.eje}
                </Typography>
            </div>
            <div
                style={{
                    padding: '7%',
                    marginTop: -30
                }}
            >
                <CardHeader
                    title={proyect.title.slice(0, 38)}
                    subheader={proyect.text.slice(0, 250)}
                    titleTypographyProps={{
                        variant: isMobile ? 'h6' : 'h5',
                        style: { color: colores.darkBlueNav }
                    }}
                />

                <Typography
                    style={{
                        color: colores.darkBlueNav,
                        marginTop: 10,
                        letterSpacing: 1.6
                    }}
                >
                    {proyect.tipo}
                </Typography>
            </div>
        </Card>
    );
};

