import {
    Button,
    CardMedia,
    Checkbox,
    Paper,
    TextField,
    Typography
} from '@material-ui/core';
import bannerJor from '../assets/portyt.png';
import hsf from '../assets/hacemosLogo.png';
import { useState } from 'react';
import { isMobile } from 'react-device-detect';

import sumateService from '../helpers/sumateService';

const FormuPart = () => {
    const [formu, setFormu] = useState({
        nombre: '',
        apellido: '',
        dni: '',
        tel: '',
        email: '',
        ayuda: ''
    });

    function onChange(value) {
        console.log('Captcha value:', value);
    }

    function validateForm() {
        return (
            formu.nombre.length > 0 &&
            formu.apellido.length > 0 &&
            formu.dni.length > 0 &&
            formu.tel.length > 0 &&
            formu.ayuda.length > 0
        );
    }

    async function handlerSubmit() {
        await sumateService.crearSumate(formu);
    }
    return (
        <>
            <div>
                <img style={{ height: 80, margin: 10 }} src={hsf} />
            </div>
            <div
                style={{
                    display: 'flex',
                    backgroundImage: `url(${bannerJor})`,
                    backgroundSize: 'cover'
                }}
            >
                <Paper
                    style={{
                        flexDirection: 'column',
                        display: 'flex',
                        width: 350,
                        marginLeft: isMobile ? 10 : 40,
                        marginTop: 20,
                        marginBottom: 10
                    }}
                >
                    <div
                        style={{
                            backgroundColor: 'rgba(61, 194, 227)',
                            height: 50
                        }}
                    >
                        <Typography
                            style={{
                                fontSize: 22,
                                padding: 10,
                                fontWeight: 'bold',
                                color: '#fff'
                            }}
                        >
                            SUMATE
                        </Typography>
                    </div>
                    <div
                        style={{
                            padding: 20,
                            display: 'flex',
                            flexDirection: 'column'
                        }}
                    >
                        {textFi.map((txtF) => (
                            <div style={{ marginBottom: 20 }}>
                                <Typography>{txtF.name}</Typography>
                                <TextField
                                    size="small"
                                    variant="outlined"
                                    type={txtF.type}
                                    onChange={(e) =>
                                        setFormu({
                                            ...formu,
                                            [txtF.id]: e.target.value
                                        })
                                    }
                                />
                            </div>
                        ))}
                        <Typography style={{ fontSize: 22 }}>
                            ¿Cómo te gustaría participar?
                        </Typography>
                        {preg.map((checks) => (
                            <div>
                                <Typography style={{ fontSize: 14 }}>
                                    <Checkbox
                                        color="primary"
                                        checked={formu.ayuda === checks}
                                        onChange={() =>
                                            setFormu({
                                                ...formu,
                                                ayuda: checks
                                            })
                                        }
                                        inputProps={{
                                            'aria-label': 'primary checkbox'
                                        }}
                                    />
                                    {checks}
                                </Typography>
                            </div>
                        ))}
                    </div>

                    <Button
                        onClick={() => handlerSubmit()}
                        color="primary"
                        variant="contained"
                        disabled={!validateForm()}
                    >
                        ENVIAR
                    </Button>
                </Paper>
            </div>
            <div
                style={{
                    position: 'absolute',
                    top: '13vh',
                    left: 20
                }}
            ></div>
            <div style={{ height: '12vh' }}>
                <img style={{ height: 80, margin: 10 }} src={hsf} />
            </div>
        </>
    );
};

const textFi = [
    { id: 'nombre', name: 'Nombre', type: 'text' },
    { id: 'apellido', name: 'Apellido', type: 'text' },
    { id: 'dni', name: 'DNI', type: 'number' },
    { id: 'tel', name: 'Numero de teléfono  móvil', type: 'number' },
    { id: 'email', name: 'Correo electrónico', type: 'email' }
];

const preg = [
    'En Redes Sociales (Voluntario Digital)',
    'Recorriendo los Barrios',
    'Armando Reuniones en mi casa',
    'Llamando a otras personas a sumarse',
    'Siendo Fiscal',
    'colaborar con alimentos o vestimenta'
];

export default FormuPart;

