import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CardNoticia from '../components/cardNoticia';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { FormContext } from '../context/formContext';
import {
    makeStyles,
    IconButton,
    Typography,
    CardMedia,
    Card,
    Chip
} from '@material-ui/core';
import colores from '../styles/colors';
import React, { useEffect, useContext, useRef, useState } from 'react';
import { isMobile } from 'react-device-detect';

const NoticiasModal = ({ open, setOpen }) => {
    const classes = useStyles();
    const { noticias } = useContext(FormContext);
    const [clickNota, setClickNota] = useState(false);

    const descriptionElementRef = useRef(null);
    useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);
    console.log(clickNota);
    return (
        <Dialog
            maxWidth="md"
            fullScreen={isMobile}
            open={open}
            onClose={() => setOpen(false)}
            scroll={'paper'}
        >
            <DialogTitle id="prensa">
                {clickNota ? (
                    <>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={() => setClickNota(false)}
                            aria-label="close"
                        >
                            <ArrowBackIcon />
                        </IconButton>
                        {clickNota.title}
                    </>
                ) : (
                    <>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={() => setOpen(false)}
                            aria-label="close"
                        >
                            <ArrowBackIcon />
                        </IconButton>
                        {clickNota.title}
                        Ultimas Noticias
                    </>
                )}
            </DialogTitle>
            <DialogContent dividers={true}>
                <DialogContentText
                    id="notas"
                    ref={descriptionElementRef}
                    tabIndex={-1}
                >
                    <div className={classes.modalCont}>
                        {!clickNota ? (
                            noticias.map((nota) => (
                                <CardNoticia
                                    data={nota}
                                    key={nota.title}
                                    setClickNota={setClickNota}
                                />
                            ))
                        ) : (
                            <NoticiaOn data={clickNota} />
                        )}
                    </div>
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <div
                    style={{
                        display: 'flex',

                        overflowX: 'auto'
                    }}
                >
                    {temasChip.map((tema) => (
                        <Chip
                            style={{ marginRight: 10, cursor: 'pointer' }}
                            label={tema}
                            variant="outlined"
                            color="primary"
                        />
                    ))}
                </div>
            </DialogActions>
        </Dialog>
    );
};

const NoticiaOn = ({ data }) => {
    const classes = useStyles();

    return (
        <>
            <Typography>{data.subtitle}</Typography>
            <Card elevation={3} className={classes.media}>
                <CardMedia image={data.img} style={{ height: '100%' }} />
            </Card>
            <Typography>{data.text}</Typography>
        </>
    );
};

const temasChip = ['Todo', 'ambiente', 'genero', 'barrios', 'covid'];

const useStyles = makeStyles((theme) => ({
    modalCont: {
        marginRight: isMobile ? null : '10%',
        marginLeft: isMobile ? null : '10%',
        '*::-webkit-scrollbar': {
            width: '0.3em'
        },

        '::-webkit-scrollbar-track': {
            background: 'transparent'
        },

        '::-webkit-scrollbar-thumb': {
            backgroundColor: 'grey',
            borderRadius: 15
        }
    },

    media: {
        height: isMobile ? 220 : 320,
        marginTop: 20,
        marginBottom: 20,
        borderRadius: 5
    }
}));

export default NoticiasModal;
