import React, { useContext } from 'react';

import { CardMedia, Fab, Typography, makeStyles } from '@material-ui/core';
import colores from '../styles/colors';
import ArtTrackIcon from '@material-ui/icons/ArtTrack';
import NoticiasModal from './noticiasModal';

export default function NoticiasView() {
    const classes = useStyles();

    const [open, setOpen] = React.useState(false);

    return (
        <div className={classes.prensaDiv}>
            <Fab
                size="large"
                variant="extended"
                className={classes.prensaBtn}
                onClick={() => setOpen(true)}
            >
                <ArtTrackIcon className={classes.prensaIco} />
                Prensa
            </Fab>

            <NoticiasModal open={open} setOpen={setOpen} />
        </div>
    );
}

const useStyles = makeStyles((theme) => ({
    modalCont: {
        marginRight: '10%',
        marginLeft: '10%',
        '*::-webkit-scrollbar': {
            width: '0.3em'
        },

        '::-webkit-scrollbar-track': {
            background: 'transparent'
        },

        '::-webkit-scrollbar-thumb': {
            backgroundColor: 'grey',
            borderRadius: 15
        }
    },
    prensaDiv: {
        marginBottom: 35,
        marginLeft: '12%'
    },
    prensaBtn: {
        backgroundColor: colores.mainLight,
        //padding: 25,
        fontSize: 18,
        color: colores.gamBdark
    },
    prensaIco: {
        marginRight: theme.spacing(1),
        color: colores.gamBdark,
        height: 35,
        width: 35
    },
    media: {
        height: 320,
        marginTop: 20,
        marginBottom: 20
    }
}));
